import * as type from "../type";


export const registerUser = (data) => (dispatch) => {
    try{
        dispatch({
            type: type.REGISTER_USER
        });
        setTimeout(()=>{
            dispatch({
                type: type.REGISTER_USER_SUCCESS,
                payload: data,
            })
        },2000);
    }
    catch(error){
        dispatch({
            type: type.REGISTER_USER_FAIL,
            error:error,
        });
    }
    
    
    // return{
    //     type: "REGISTER_USER",
    //     payload: data,
    // };
}

export const loginUser = (data) => {
    return{
        type: "LOGIN_USER",
        payload: data,
    }
}