import React from 'react';
import {connect} from "react-redux";
import { registerUser } from "../../actions";
import classes from './register.module.css';
import {Link} from "react-router-dom";

class Register extends React.Component{

    addUser(){
        const nameInput = this._inputVal.value;
        const passInput = this._inputPass.value;

        if(nameInput !== "" && passInput !== ""){
            const data = {
                name: nameInput,
                pass: passInput,
                key: Date.now(),
            }
            this.props.registerUser(data);
        }

        this._inputVal.value = "";
        this._inputPass.value = "";
        
    }

    render(){
        return(
            <div className={classes.register_section}>
                <h1>Register</h1>

                <div className={classes.form_section}>
                    <h3>Username</h3>
                    <input type="text" placeholder="Insert Username" ref={(a)=>(this._inputVal = a)}/>
                    <h3>Password</h3>
                    <input type="password" placeholder="Insert Password" ref={(b)=>(this._inputPass = b)}/>
                    <Link to="/" ><button onClick={()=> this.addUser()}>Sign Up!</button> </Link>
                </div>
                
            </div>
        )
    }
}
const mapStatetoProps = (state) => ({
    userData: state.user,
});

const mapDispatchtoProps = {
    registerUser:registerUser,
}



export default connect(mapStatetoProps,mapDispatchtoProps)(Register);