import React from 'react';
import {connect} from "react-redux";
import { loginUser } from "../../actions";
import classes from "./login.module.css";
import {Link , Navigate} from "react-router-dom";

class Login extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            status: false,
        }
    }
    loginUser(){
        //get the data from array, check the array if username and password already exist, if match then log in
        const nameInput = this._nameInput.value;
        const passInput = this._passInput.value;
        // if(nameInput !== "" && passInput !== ""){
        //     const data = {
        //         name: nameInput,
        //         pass: passInput,
        //         key: Date.now(),
        //     }
        //     this.props.loginUser(data);
        // }

        // this._nameInput.value = "";
        // this._passInput.value = "";
        console.log(this.props.userData.data);
        this.props.userData.data.map(
            (list) => {if(list.name === nameInput && list.pass === passInput){
                this.setState({status:true});
            }
        })
    
    }

    render(){
        if(this.state.status){
            return <Navigate to="/homepage" />
        }
        else{
            return(
                <>

                <div className={classes.login_section}>
                    <h1>Login</h1>
    
                    <form>
                        <h3>Username</h3>
                        <input type="text" placeholder="Insert Username..." ref={(a)=>(this._nameInput = a)}/>
                        <h3>Password</h3>
                        <input type="password" placeholder="Insert Password..." ref={(b) => (this._passInput = b)}/>
                    </form>
                    <button onClick={()=>this.loginUser()}>Submit</button>
                    <Link to="/register">Sign Up!</Link>
    
                </div>

                </>
                
    
            )
        }
        
    }
}

const mapStatetoProps = (state) => ({
    userData: state.user,
});

const mapDispatchtoProps = {
    loginUser:loginUser,
}



export default connect(mapStatetoProps,mapDispatchtoProps)(Login);