import * as type from "../type";

const defaultState = {
    isLoading: false,
    data: [],
    error:null,
};

const registerUser = (state,action) => {
    switch(action.type){
        case type.REGISTER_USER:
            return {
                isLoading: true,
                data: [...state.data],
                error: null,
                
            };
        case type.REGISTER_USER_SUCCESS:
            return{
                isLoading:false,
                data: [...state.data,action.payload],
                error: null,
            }
        case type.REGISTER_USER_FAIL:
            return{
                isLoading: false,
                data: [...state.data],
                error: action.error,
            }
        default:
            return defaultState;
    }
}
export default registerUser;